import React from "react";
import { Link } from "react-router-dom";
import './ButtonMailto.css';

const ButtonMailto = ({ mailto, label }) => {
    return (
        <Link
            class='email'
            to='#'
            onClick={(e) => {
                window.location = mailto;
                e.preventDefault();
            }}
        >
            {label}
        </Link>

    );
};

export default ButtonMailto;