import { useContext } from "react";
import image from './visual/picture.jpeg';
import './AboutSection.css';
import DotRing from './DotRing';
import { MouseContext } from "../components/context/mouse-context";

function AboutSection() {
  const { cursorType, cursorChangeHandler } = useContext(MouseContext);
  return (

    <div className='main-about-hero'>
   <DotRing />
       <div className='r-side'>
      <div className='image-about-section'>
      <div
          onMouseEnter={() => cursorChangeHandler("hovered")}
          onMouseLeave={() => cursorChangeHandler("")}
        >
        <img className='image-about' src={image} alt='portfolio' />
        <span className="motto-text">"Thinking imaginatively and seeing the world in a new way". </span>
        </div>
      </div>
    </div>

      <div className='l-side'>
        <div className='text-about-section'>
          <p>IT Engineering student with specialization in Media Technology highly competent in customer service with 4 years of experience in leading multicultural teams. I have an extensive experience in group work and communicating with people from different cultures and backgrounds. When it comes to managing people, my main priority is to find strengths and inspire to achieve the goals.</p>
          <br></br><p>I get my inspiration and new ideas by discovering the world and expanding my own boundaries. This is why I traveled extensively during my studies. I've done internships in Germany and China. I also completed a winter semester at Mount Royal University in Calgary, Canada.</p>
          <br></br><p>I'm constantly looking for new challenges to help me grow. Overcoming the difficult challenges only strengthens and develops me personally and professionally. I am using knowledge, facts and data analysis to evaluate problems and find effective and creative solutions. </p>
          <br></br><p>My passion is UX, digital and web design. I use design to communicate ideas and turn new ideas into reality. I believe that good visual can add value.</p>
        </div>   
      
    </div>
    
  
    
    </div>
      

  );
}

export default AboutSection;